import { createStore, applyMiddleware, compose, combineReducers, Store, StoreEnhancer, StoreEnhancerStoreCreator, ReducersMapObject } from 'redux';
//import thunk from 'redux-thunk';
import { routerReducer, routerMiddleware } from 'react-router-redux';
import * as StoreModule from './store';
import { ApplicationState, reducers } from './store';
import { History } from 'history';

export default function configureStore(history: History, initialState?: ApplicationState) {
    // Build middleware. These are functions that can process the actions before they reach the store.
    const windowIfDefined = typeof window === 'undefined' ? null : window as any;
    // If devTools is installed, connect to it
    const devToolsExtension = windowIfDefined && windowIfDefined.__REDUX_DEVTOOLS_EXTENSION__ as () => StoreEnhancer;
    //const createStoreWithMiddleware = compose(
    //    applyMiddleware(thunk, routerMiddleware(history)),
    //    devToolsExtension ? devToolsExtension() : <S>(next: StoreEnhancerStoreCreator<S>) => next
    //)(createStore);
    const createStoreWithMiddleware = compose(
        applyMiddleware(routerMiddleware(history)),
        devToolsExtension ? devToolsExtension() : <S>(next: StoreEnhancerStoreCreator<S>) => next
    )(createStore);

    // Combine all reducers and instantiate the app-wide store instance
    const allReducers = buildRootReducer(reducers);

    /*
     * inital any casting suggested as fix from https://stackoverflow.com/questions/46266536/resolve-cannot-invoke-an-expression-whose-type-lacks-a-call-signature-typescri?utm_medium=organic&utm_source=google_rich_qa&utm_campaign=google_rich_qa
     */
    const store = (<any>createStoreWithMiddleware)(allReducers, initialState) as Store<ApplicationState>;

    // Enable Webpack hot module replacement for reducers
    if (module.hot) {
        module.hot.accept('./store', () => {
            const nextRootReducer = require<typeof StoreModule>('./store');
            store.replaceReducer(buildRootReducer(nextRootReducer.reducers));
        });
    }

    return store;
}

function buildRootReducer(allReducers: ReducersMapObject) {
    /*
     * 'as any' suggested as temp fix from https://github.com/reduxjs/redux/issues/2709
     */
    return combineReducers<ApplicationState>(Object.assign({}, allReducers, { routing: routerReducer }) as any);
}
